import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import Workout from '../../components/workout';

const initialState = {};

function getWorkoutColor(workout) {
	switch (workout.type) {
		case 1:
			return 'border-primary';	
		case 2:
			return 'border-danger';
		case 3:
			return 'border-success';
		default:
			break;
	}
}

function getWorkoutIcon(workout) {
	switch (workout.type) {
		case 1:
			return 'fas fa-running';
		case 2:
			return 'fas fa-flag-checkered';
		case 3:
			return 'fas fa-hiking';
		default:
			break;
	}
}

@observer
class Workouts extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.loadWorkouts();
    }

	loadWorkouts() {
		const { workoutStore } = this.props.stores;
		workoutStore.load();
	}

	render() {
		const { trackStore, workoutStore, userStore, dogStore } = this.props.stores;
		const { workouts } = workoutStore;
		const { tracks } = trackStore;
		const { dogs } = dogStore;
		const { user } = userStore;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
				<div class='row'>
					<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<a class='btn btn-primary btn-sm float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div>
					<div class='col-12'>
						<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 px-0'>
							<h5>Treningsturene - {workouts.length} stk</h5>
						</div>

						{workouts && workouts.length === 0 && (
							<div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 text-muted'>
								{dogs && dogs.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-dog' />
										</div>
										<h5>
											Ingen hunder er registert.
										</h5>
										Det er lurt å legge inn hunder før du registrerer en treningstur.<br />
										<a class='btn btn-info mt-3' href='/dogs/edit/new'>
											<i class='fas fa-dog' /> Legg til hund
										</a>
									</div>
								)}

								{tracks && tracks.length === 0 && (
									<div class='mb-5 py-3 text-center border-top border-bottom'>
										<div class='display-4'>
											<i class='fas fa-map-marked' />
										</div>
										<h5>
											Ingen runder er registert.
										</h5>
										Det er lurt å legge inn runden før du registrerer en treningstur.<br />
										<a class='btn btn-info mt-3' href='/tracks/edit/new'>
											<i class='fas fa-map-marked' /> Legg til runde
										</a>
									</div>
								)}

								<div class='text-center mb-5'>
									<div class='display-1'>
										<i class='fas fa-running' />
									</div>
									<h5>
										Ingen treningsturer er registrert ennå.
									</h5>
								</div>

								Slik kommer du i gang:
								<ol>
									<li>Trykk på knappen "Legg til treningstur" oppe til høyre.</li>
								</ol>
							</div>
						)}

						{workouts && workouts.map(workout => (
							<Workout stores={this.props.stores} workout={workout} />
						))}
					</div>
					{/* <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
						<a class='btn btn-primary btn-sm mt-5 float-right'
							href='/workouts/edit/new'
						>
							<i class='fas fa-plus' /> Legg til treningstur
						</a>
					</div> */}
				</div>
			</div>
		);
	}
}

export default Workouts;
